body {
        margin: 0;
        overflow-x: hidden;
        background-image: linear-gradient(white, #77AFDF(250, 225, 179));
}

html {
    font-family: 'Raleway', sans-serif;
}
.letraindex {
    font-size: 20px;
}

pre {
    font-family: 'Raleway', sans-serif; 
}

/* CABECERA Y LOGO*/
.text_inicial{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-around;
    margin: 0 auto;
    width:100%; /* Establecemos que el ancho sera del 100% */
    max-width:100%; /* Aqui le decimos que el ancho m√°ximo sera de 1000px */
    overflow:hidden; /* Eliminamos errores de float */
    /*background:#252932;*/
    font-size: 21px;
    line-height:20px;
    color: black;
    font-family: 'Raleway', sans-serif;
}

.logo {
    text-align: left;
    background-color: transparent;  
    margin-top: 40px;
    margin-left: 20px; 
    height: 100px;
    width: auto;
}

/*NAV HOME*/

* {
    margin: 0;
    padding: 0;
}

header {
	width:100%; /* Establecemos que el header abarque el 100% del documento */

}

header nav {
	width:90%;
	max-width:1000px;
	margin:20px auto;
    background:white;
}
 
.menu_bar {
	display:none;
}
 
header nav ul {
	overflow:hidden;
	list-style:none;
}
 
header nav ul li {
	float:left;
}
 
header nav ul li a {
    color: #77AFDF;
	padding:20px;
	display:block;
	text-decoration:none;
}
 
header nav ul li span {
	margin-right:10px;
}
 
header nav ul li a:hover {
    background:white;
}
 
section {
	padding:20px;
}
 
@media screen and (max-width:800px ) {
	header nav {
		width:80%;
		height:100%;
		left:-100%;
		margin:0;
		position: fixed;
	}
 
	header nav ul li {
		display:block;
		float:none;
		border-bottom:1px solid rgba(255,255,255, .3);
	}
 
	.menu_bar {
		display:block;
		width:100%;
		background:white;
	}
 
	.menu_bar .bt-menu {
		display:block;
		padding:20px;
		background:white;
		color: #77AFDF;
		text-decoration:none;
		font-weight: bold;
		font-size:25px;
		-webkit-box-sizing:border-box;
		-moz-box-sizing:border-box;
		box-sizing:border-box;
	}
 
	.menu_bar span {
		float:right;
		font-size:40px;
	}
}

